import React,{useState} from 'react';

const Form = ( ) => {
    const [userRegistration,setUserRegistration]=useState(
        {
            FirstName:"",
            LastName:"",
            email:"",
            subject:"React"
        }
    );

    const [records,setRecords]=useState([]);
    const handleInput=(e) =>{
        const name=e.target.name;
        const value=e.target.value;
        console.log(name,value);
        setUserRegistration({...userRegistration,[name] :value})

    }

    
    const handleSubmit =(e) => {
        e.preventDefault();

        const newRecord ={ ...userRegistration, id:new Date().getTime().toString()}
        setRecords([...records,newRecord])
    }
    return (
        <div class="form-container">
            <h1 align="center"><font color="red">Registration</font></h1>
            <form action="" onSubmit={handleSubmit}>
            <div >
                <label htmlFor="FirstName">First Name:-</label>
                <input type="text" autoComplete="off" name="FirstName" 
                value={userRegistration.FirstName}
                onChange={handleInput}
                id="FirstName"></input>
            </div>

            <div>
                <label htmlFor="LastName">Last Name:-</label>
                <input type="text"  autoComplete="off" name="LastName" 
                value={userRegistration.LastName}
                onChange={handleInput}
                id="SecondName"></input>
            </div>

            <div>
                <label htmlFor="Email">Email:-</label>
                <input type="text" autoComplete="off" name="email" 
                value={userRegistration.email}
                onChange={handleInput}
                id="Email"></input>
            </div>

            <div>
                <label htmlFor="subject">Subject:-</label>
                
                <select name="subject" value={userRegistration.subject}
                onChange={handleInput}>
                    <option value='React'>React</option>
                    <option value='Angular'>Angular</option>
                    <option value='NodeJs'>Node Js</option>
                    <option value='PHP'>PHP</option>
                </select>
            </div>

            <div id="buttonpos">
                <button type="submit" >Submit</button>
            </div>
          
            </form>

          
            <div>
                {
                    records.map((curElem)=>{
                        const{id,FirstName,LastName,email,subject}=curElem;
                        return(
                            
                            <div key={curElem.id}>
                             <h3 align="center"><font color="blue">Your Response Form:</font></h3>   
                            <p>FirstName: {FirstName}</p>
                            <p>LastName: {LastName}</p>
                            <p>Email: {email}</p>
                            <p>Subject: {subject}</p>
                        </div>
                        )
                        
                        
                    }

                    )
                }
            </div>
            </div>    
      
    )
}

export default Form;